import React from 'react';
import { Grid, Text, Button } from '../../../style';
import * as  Yup from 'yup';
import { useFormik } from 'formik';
import { Switch, InputNumber, Input, Select } from 'antd';
import { Tooltip } from 'antd';
import { Garage, GarageOccupancyTable } from '../../../model/domain/garage';

const validationSchema = Yup.object({
    name: Yup.string().required("Required!").min(6, "Garage must contain at least 6 characters"),
    maxOccupancy: Yup.number().required("Required!")
});

const sources = {
    integration: 'integration',
    table: 'table'
}
const defaultValue = { name: "", active: false, maxOccupancy: 420, occupancyTableId: null, occupancyUpdateSource: sources.table };


const GarageInfoForm = (props: any) => {
    const { loading, tables = [], garage } = props;

    const { handleSubmit, handleChange, setFieldValue, values, handleBlur } = useFormik({
        initialValues: garage || defaultValue as Garage,
        onSubmit(values) {
            props.submit(values);
        },
        validationSchema
    })

    const error = values.name < 6;
    return (
        <form
            onSubmit={handleSubmit}
            className="shake"
            style={{
                height: "100%",
                display: "grid",
                background: "white",
                padding: "2em",
                width: "100%",
                placeItems: "center stretch",
                gridGap: ".4em"
            }}>

            <div style={{ placeSelf: "stretch", display: "grid", placeItems: "stretch" }} >
                <Text textAlign="left" bold>Enter the Name of your Garage<span className="red">*</span></Text>
                <Input
                    style={{ placeSelf: "end" }}
                    type={'text'}
                    name={'name'}
                    onBlur={handleBlur}
                    suffix={
                        <Tooltip title={"Garage must contain at least 6 characters"}>
                            <i className={`fa fa-${error ? 'times-circle red' : 'check green'}`} style={{ position: "relative" }}></i>
                        </Tooltip>
                    }
                    value={values.name}
                    onChange={handleChange}
                    placeholder={"Example: SJSU Garage 1"} />
            </div>

            <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                <Text textAlign="left" placeSelf="end start" bold>
                    Activate Garage
                        <span className="red">*</span>
                </Text>
                <Switch
                    checked={values.active}
                    style={{ placeSelf: "end" }}
                    onChange={(checked: boolean) => setFieldValue("active", checked)}
                />
            </Grid>

            <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                <Text textAlign="left" placeSelf="end start" bold>
                    Automate Garage 
                    <span className="red">*</span>
                </Text>
                <Switch
                    checked={values.occupancyUpdateSource === sources.integration}
                    style={{ placeSelf: "end" }}
                    onChange={(checked: boolean) => 
                        setFieldValue("occupancyUpdateSource", checked? sources.integration: sources.table)
                    }
                />
            </Grid>

            <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0">
                <Text textAlign="left" placeSelf="end start" bold>
                    Garage Occupancy Table
                </Text>
                <Select
                    defaultValue={values.occupancyTableId}
                    onChange={(id: string) => setFieldValue("occupancyTableId", id === 'null'? null: id)}
                >   
                    <Select.Option value={'null'}>{"None"}</Select.Option>
                    {tables.map(({ id, title }: GarageOccupancyTable) => <Select.Option value={id}>{title}</Select.Option>)}
                </Select>
            </Grid>

            <Grid margin=".6em 0" placeSelf="stretch" placeItems="stretch" gridGap="0" customCols="1fr auto">
                <Text textAlign="left" placeSelf="end start" bold>
                    Max Garage Occupancy
                        <span className="red">*</span>
                </Text>
                <InputNumber
                    min={1}
                    style={{ placeSelf: "end" }}
                    value={values.maxOccupancy}
                    onChange={(e) => setFieldValue("maxOccupancy", e)}
                    name={"maxOccupancy"}
                />
            </Grid>
            {loading ? <Button loading disabled style={{ fontSize: ".8em" }}>Loading</Button> :
                <Button.Normal
                    style={{ fontSize: ".8em" }}
                    type="submit"
                    disabled={error}
                    onSubmit={(e: any) => handleSubmit(e)}>
                    Confirm
                </Button.Normal>}
        </form>
    );
};

export default GarageInfoForm;