import React, { useState, useEffect } from 'react'
import { Grid, Header } from '../../style';
import CountUp from 'react-countup';
import { Progress, Tooltip } from 'antd';
import API from '../../api';
import { useSelector } from 'react-redux';
import { disabledTxtColor } from '../../config';

export const ScoreCard = ({ title, faIcon, span = 1, color = 'white', children, label, borderRight }: any) => {
  const borderStyle = borderRight ? { borderRight: '1px solid ' + disabledTxtColor, padding: '0 1em' } : {};
  return (
      <Grid 
        className='noselect' 
        style={{ ...borderStyle, gridColumn: 'span ' + span }} 
        placeSelf='stretch' 
        customRows='auto 1fr'
      >
          <Header size={3.5} color={color} placeSelf='start stretch'>
              {title} &nbsp;
          {faIcon &&
                  <Tooltip placement="topLeft" title={label}>
                      <i className={faIcon + ' pointer'}></i>
                  </Tooltip>}
          </Header>
          <div style={{ color, placeSelf: 'start center', }}>
              {children}
          </div>
      </Grid>);
}

ScoreCard.PercentBar = ({ percent, type, text, status = false }: any) => {
  const gradient = percent <= 50? '#F2BE72' : 
          `linear-gradient(to right, #F2BE72 ${50}%, #F76E49)`;
  return (
  <Progress 
      percent={percent} 
      strokeColor={status? gradient : '#5578D6'}
      successPercent={0}
      type={type}
      format={() => <div className='bold' style={{ color: 'white' }}>{text || (percent + '%')}</div>}
  />);
}

const intoHeadings = (str: string) => str.charAt(0).toUpperCase() + str.substring(1).replace(/ /g, ' ');

const FlatValue = ({ name, flatValueQuery, leftBorder, borderRight }: any) => {
  const [value, setValue] = useState<number | 'N/A'>(0);
  const [isPercent, setIsPercent] = useState(false);

  const eId = useSelector((state: any) => state.user.enterprise.id);
  useEffect(() => {
    API.getFlatValue(eId, flatValueQuery)
      .then((res: any) => {
        if (res?.success) {
          setIsPercent(res?.flatValueResult?.metricType?.split('_')?.includes('percent'))
          setValue(res.flatValueResult.flatValue.value || 0)
        } else throw new Error("Failed")
      })
      .catch(() => setValue("N/A"))
  })

  return (
    <ScoreCard borderRight={borderRight} title={intoHeadings(name)} faIcon='fa fa-car' leftBorder={leftBorder}>
      {isPercent ?
      <ScoreCard.PercentBar 
        type="circle"
        percent={Number(value)}
      />
        : <div className='txt-lgx'>{
          typeof value === "number" ?
            <CountUp duration={2} start={0} end={value as number} /> : value}
        </div>
      }
    </ScoreCard>
  );
};

ScoreCard.FlatValue = FlatValue;