import React from 'react';
import { Grid, Button } from '../../../style';
import Chart from '../../../components/chart';
import { useMetricData } from '../../../hooks/metrics';
import Filter from './filter';
import { useSelector } from 'react-redux';
import { TimeSeries } from '../../../model/domain/metrics';
import { defaultTimeSeries } from '../../../hooks/timeseries';

const chart = {
  title: "Occupancy",
  timeSeriesQuery: {
    metricType: "garage_occupancy_count",
    resourceIds: [],
    crossSeriesReducer: "NONE",
    alignment: null
  },
  backgroundColor: "#FFFFFF",
  xAxisLabel: "T",
  yAxisLabel: "Occupancy",
}
const Analytics = ({ filterWidth, open, setOpen } : any) => {
  const eId = useSelector(({ user }: any) => user.enterprise.id);
  const { metricDataStatus, refresh } = useMetricData({
    timeseries : defaultTimeSeries,
    eId
  });

  const submit = (ts: TimeSeries.Query)=> refresh(ts)
  const props = {
    defaultValue : defaultTimeSeries,
    metricDataStatus,
    filterWidth,
    loading: metricDataStatus.loading,
    submit
  }

  return (
    <Grid placeItems="start stretch" padding="1em" height="100%">
      <Grid cols="1" gridGap="0 1em">
            <Chart
                className='slide-up' 
                filterWidth={ filterWidth}
                metricDataStatus={metricDataStatus}
                {...chart}
                filter={
                  !open? <Button 
                  placeSelf="end"
                  rounded
                  shadow
                  minWidth="3em"
                  onClick={()=>setOpen(!open)}
                  faIcon={`fa fa-filter`}
                /> : null}    
                legend={true}
              />
          <Filter {...props} open={open} toggle={()=>setOpen(false)} />
      </Grid>
    </Grid>
  );
}

export default Analytics;
