import React from 'react'
import { AreaStack } from '@vx/shape';
import { colors } from './colors.json';

export interface GraphEvent {
    onTouchStart : (e : any) => void;
    onTouchMove : (e : any) => void;
    onMouseMove : (e : any) => void;
    onMouseLeave : (e : any) => void;
};
export type StackedAreasProps = {
    width? : number;
    xScale: any;
    yScale: any;
    isSelected?: any;
    noneSelected?: any;
    points: any;
    event : (i: number ) => GraphEvent
};

export function StackedArea({  xScale, yScale, points, isSelected, noneSelected, event}: StackedAreasProps) {
  const getColor = (index: number) => colors[index % colors.length];
  const keys = Object.keys(points[0]).filter(k => k !== 'date') as any[];
  const getDate = (d: any) => (new Date(d.date) as Date).valueOf();
  const getY = (d: any, index: number) => d[index];

  return (
    <AreaStack
      keys={keys}
      data={points}
      x={d => xScale(getDate(d.data))}
      y0={d => yScale(getY(d, 0))}
      y1={d => yScale(getY(d,1))}
    >
      {({ stacks, path }) =>
        stacks.map((stack, index) => {
          const opacity = isSelected(index)? '70' : noneSelected ? '50': '25';
          const color = getColor(index) + opacity;
          return (
            <path
              key={`stack-${index}`}
              d={path(stack) || ''}
              stroke={color}
              strokeWidth={2}
              {...event(index)}
              fill={color}
            />
          )
        })
      }
    </AreaStack>);
}

export default StackedArea;