import React, { useContext } from 'react';
import Layout from '../../layout';
import { Grid, Placeholder, Header, Button, List } from '../../style';
import { XyChart } from '../../model/domain/dashboard';
import { useSelector } from 'react-redux';
import ScoreCards from '../../components/score-card/score-cards';
import { useWidgets } from '../../hooks/widget';
import { Link } from 'react-router-dom';
import { ComponentsContext } from '../../context/components';
import Chart from '../../components/chart';
import useTimeseries from '../../hooks/timeseries';
import { useMetricData } from '../../hooks/metrics';
import { gradient } from '../../config';

const Overview = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const name = useSelector((state: any) => state.user.user.shortName);
    const { charts, loading, scoreCards, scoreCardOptions, editScoreCards, removeScoreCard } = useWidgets({ eId, screen: "overview" });
    const hideOnLoading = loading ? 'hide' : '';
    const { timeseries } =  useTimeseries();
  
    const { metricDataStatus, interval } = useMetricData({ timeseries: timeseries, eId});
    
    const props = {
      chart : timeseries,
      metricDataStatus,
      interval
    }
    return (
        <Layout sidebar>
            <Layout.Top>
                <Header
                    color="white"
                    margin="1em 0 0 0"
                    className="capitalize"
                    width="100%"
                    bold
                    textAlign="left"
                >
                    &nbsp; Welcome, {name}
                </Header>
                <div></div>
                <ScoreCards
                    loading={loading}
                    eId={eId}
                    removeScoreCard={removeScoreCard}
                    editScoreCards={editScoreCards}
                    scoreCards={scoreCards}
                    scoreCardOptions={scoreCardOptions}
                />
            </Layout.Top>
            <Layout.Bottom>
                <Grid cols="2" margin='2em 0 0 0' gridGap='0.3em'>
                    <Header
                        className={hideOnLoading}
                        color="white"
                        style={{ gridColumn: "1/-1" }}
                        bold
                        width="100%"
                        textAlign="left"
                    >
                     &nbsp; Today's Garage Occupancy Overview
                    </Header>
                    <List    
                        list={charts}
                        loader={<Placeholder
                            lines={7}
                            number={1}
                            style={{ minHeight: "400px", minWidth: "100%", gridColumn: '1/-1' }}
                        />}
                        loading={loading}
                        map={((item: XyChart) => <Chart {...props} {...item.lineChart as any} filter={false} span={2} />)}
                    />
                    <Link
                        to="/garages"
                        className={hideOnLoading}
                        style={{ gridColumn: "1/-1", placeSelf: "start" }}
                    >
                        <Button
                            rounded
                            bold
                            padding=".5em"
                            shadow
                            noHover
                            bg={gradient}
                        >
                            See Details &nbsp; <i className="fa fa-chevron-right"></i>
                        </Button>
                    </Link>
                </Grid>
            </Layout.Bottom>
        </Layout>
    );
}
export default Overview;