import React, { useState } from 'react';
import { Header, Card, Text, Button, Grid } from '../../../style';
import { DatePicker, TimePicker, Divider, Select } from "antd";
import { moment, Moment, FORMAT_TIME } from '../../../utils/time';
import useGarages from '../../../hooks/garage';
import { useSelector } from 'react-redux';
import { Garage } from '../../../model/domain/garage';
import { timeframes, metrics, aggregations } from './filter-options.json';
import useTimeseries from '../../../hooks/timeseries';

const Filter = ({ defaultValue, open, toggle, filterWidth,  loading : loadingTimeSeries, submit }: any) => {
    const eId = useSelector(({  user }: any) => user.enterprise.id);
     const { timeseries, actions } =  useTimeseries(defaultValue);
    const { garages, loading } = useGarages({ eId });
    const [ active, setActive ] = useState(0);

    const style = { height: "calc(100% - 4em)", width: filterWidth, position: "fixed", zIndex: 5, top: '4em', right: 0 };
    const disabledDate = (cur: Moment | null) => cur ? cur > moment() : false;
    const props = { style: { width: '100%' }, disabled : loadingTimeSeries };
    const dividerStyle = { marginBottom: '.5em', fontSize: '.95em', color: 'gray' }

    const handlePresetChange = (e: number) => {
        setActive(e);
        const endTime = moment();
        const startTime = endTime.clone().add(-e, 'day');
        actions.setInterval({ endTime, startTime })
    }
  
    if (!open) return null;
    return (
        <Card grid shadow style={style} margin="0" placeItems="stretch" gridGap='0' >
            <Header textAlign="left" className="slide-up">
                <i className="fa fa-times pointer" onClick={toggle}></i>
                <span className="pad">Filters</span>
            </Header>
            <div>
                <Divider style={dividerStyle}>Timeframe </Divider>
                <div className="slide-up">

                    <Text textAlign="left" bold>Presets</Text>
                    <Select
                        onChange={handlePresetChange}
                        {...props}
                        value={active}
                    >
                        {timeframes.map(({ value, label }) => <Select.Option value={value}>
                            {label}
                        </Select.Option>)}
                    </Select>
                </div>


                <div className="slide-up" style={{ marginTop: '1em' }}>
                    <Text textAlign="left" bold>From</Text>
                    <Grid cols="2" gridGap='0 1em' mobileLayout={false}>
                        <DatePicker
                            disabled={active !== 0}
                            disabledDate={disabledDate}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(startTime) => actions.setInterval({ startTime })}
                            value={moment(timeseries.interval.startTime)}
                        />
                        <TimePicker
                            disabled={active !== 0}
                            disabledDate={disabledDate}
                            use12Hours
                            format={FORMAT_TIME}
                            minuteStep={15}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(startTime) => actions.setInterval({ startTime })}
                            value={moment(timeseries.interval.startTime)}
                        />
                    </Grid>
                </div>

                <div className="slide-up" style={{ marginTop: '1em' }}>
                    <Text textAlign="left" bold>
                        To
                     </Text>
                    <Grid cols="2" gridGap='0 1em' mobileLayout={false}>
                        <DatePicker
                            disabled={active !== 0}
                            disabledDate={disabledDate}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(endTime) => actions.setInterval({ endTime })}
                            value={moment(timeseries.interval.endTime)}
                        />

                        <TimePicker
                            disabledDate={disabledDate}
                            use12Hours
                            format={FORMAT_TIME}
                            disabled={active !== 0}
                            style={{ borderRadius: 8, width: '100%' }}
                            onChange={(endTime) => actions.setInterval({ endTime })}
                            value={moment(timeseries.interval.endTime)}
                        />
                    </Grid>
                </div>
            </div>
            <div className="slide-up">
                <Divider style={dividerStyle}>Metric</Divider>
                <Text bold textAlign="left">Select Measurement</Text>
                <Select
                    {...props}
                    placeholder="Select"
                    value={timeseries.metricType}
                    onChange={actions.setMetric}
                    loading={loading}
                >
                    {metrics.map(({ label, value }) => (
                        <Select.Option
                            className='capitalize'
                            value={value}
                        >
                            {label}
                        </Select.Option>))
                    }
                </Select>
            </div>

            <div className="slide-up">
                <Divider style={dividerStyle}>Resources</Divider>
                <Text bold textAlign="left">Select Garages</Text>
                <Select
                    {...props}
                    mode="multiple"
                    placeholder="All Garages"
                    value={timeseries.resourceIds}
                    onChange={actions.setGarages}
                >
                    {garages.map(({ name, id }: Garage) => <Select.Option value={id}>{name}</Select.Option>)}
                </Select>
            </div>

            <div className="slide-up">
                <Divider style={dividerStyle}>Aggregation</Divider>
                <Text bold textAlign="left">Max/Min Occupancy Selection</Text>
                <Select
                    onChange={(e: any) => actions.setAlignment(JSON.parse(e))}
                    {...props}
                    defaultValue={"None"}
                >
                    {aggregations.map(({ value, label }) => <Select.Option value={JSON.stringify(value)}>
                        {label}
                    </Select.Option>)}
                </Select>
            </div>

           <Grid cols={2} placeItems='stretch' placeSelf='end stretch'>
                <Button 
                    shadow 
                    onClick={()=>submit(timeseries)} 
                    disabled={loadingTimeSeries}
                >
                    Submit
                </Button>
                <Button 
                    onClick={()=>submit(defaultValue)} 
                    disabled={loadingTimeSeries}
                    noBorder 
                    invert 
                    shadow
                >
                    Reset
                </Button>
          </Grid>
        </Card>
    );
}

export default Filter;