import React, { useState, useEffect, useContext } from "react";
import Layout from "../../layout";
import { Grid, Button, Modal, Switch, Empty } from "../../style";
import Table from "./table";
import { primaryColor} from "../../config";
import img from "../../assets/announ.svg";
import { Announcement } from "../../model/domain/announcements";
import { useSelector, useDispatch } from "react-redux";
import { getAnnouncements, addAnnouncement, deleteAnnouncement, editAnnouncement } from "../../store/actions/announcement";
import Form from "./form";
import { useWidgets } from "../../hooks/widget";
import { ComponentsContext } from "../../context/components";
import ScoreCards from "../../components/score-card/score-cards";

const states = {
  ACTIVE: "ACTIVE",
  ALL: "ALL",
  EXPIRED: "EXPIRED",
  DEFAULT: "ACTIVE",
  states: ["ACTIVE", "ALL", "EXPIRED"],
};

const AnnoucementEl = () => {
  const eId = useSelector((state: any) => state.user.enterprise.id);
  const { editMode, toggleEditMode } = useContext(ComponentsContext);
  const screen = "announcement";
  const resource = "announcement";

  const {
    loading,
    scoreCards,
    scoreCardOptions,
    editScoreCards,
    removeScoreCard,
  } = useWidgets({ eId, screen, resource });

  const announcements = useSelector(
    (state: any) => state.announcement.announcements
  );
  const dispatch = useDispatch();

  const [state, setState] = useState({ active: states.DEFAULT });
  const add = (item: Announcement) => dispatch(addAnnouncement(eId, item));
  const edit = (item: Announcement) => dispatch(editAnnouncement(eId, item));

  const AddModal = Modal({
    title: "Add Announcement",
    message: <Adder add={add} close={() => AddModal.close()} />,
    hideCancel: true,
  });

  const open = (selected: Announcement) => {
    const EditModal = Modal({
      title: "Edit Announcement",
      message: <Adder
        add={edit}
        initialValues={selected}
        close={() => EditModal.close()}
      />,
      hideCancel: true,
    });
    EditModal.open();
  }

  const currentAnnouncements = announcements.filter(
    ({ status }: any) =>
      status.toUpperCase() === state.active || state.active === states.ALL
  );

  useEffect(() => {
    if (eId) dispatch(getAnnouncements(eId));
  }, [eId]);

  return (
    <Layout sidebar>
      <Layout.Top style={{paddingTop: '3em'}}>
        <ScoreCards
          loading={loading}
          removeScoreCard={removeScoreCard}
          editScoreCards={editScoreCards}
          scoreCards={scoreCards}
          scoreCardOptions={scoreCardOptions}
        />
        </Layout.Top>
      <Layout.Bottom>
      <Table 
        edit={open} 
        add={AddModal.open}
        toggler={
          <Switch
            style={{margin: "0 0 .5em 0"}}
            active={state.active}
            items={states.states}
            onClick={(active: string) => setState({ ...state, active })}
          />
        }
        announcements={currentAnnouncements}
      />
      </Layout.Bottom>
    </Layout>
  );
};

const Adder = (props: any) => {
  return (
    <Grid customCols="3fr 2fr" width="100%" height="100%" gridGap="0">
      <div style={{ background: primaryColor, height: "100%", padding: "1em", width: "100%" }}>
        <img src={img} style={{ maxWidth: "300px" }} />
      </div>
      <Form {...props} />
    </Grid>
  );
};

export default AnnoucementEl;