import React, { useState, useEffect } from 'react';
import { Grid, Text } from '../../../style';
import SimpleMap from './google-map';
import car from "../../../assets/car.svg";
import useGarages from '../../../hooks/garage';
import { Progress } from 'antd';
import GarageInfoForm from './garage-form';

const CreateGarage = ({ submit }: any) => {
    const [count, setCount] = useState(0);
    useEffect(() => {
        const interval = setInterval(() => {
            setCount(count => count + 5);
        }, 300);

        return () => {
            clearInterval(interval);
        };
    }, []);

    useEffect(() => {
        if (count === 90) {
            submit();
        }
    }, [count]);

    return (
        <Grid
            width="100%"
            background="white"
            height="100%"
        >
            <div style={{ width: "100%" }}>
                <Text bold>Creating your Garage</Text>
                <Progress style={{ width: "70%" }} percent={count} />
            </div>
        </Grid>);
}
export const GarageMultiStep = ({ close, refresh, eId, tables }: any) => {
    const [state, setState] = useState({
        step: 0,
        completed: false
    });

    const { addGarage } = useGarages({ eId });

    const [garage, setGarage] = useState<any>({
        name: "",
        active: false,
        maxOccupancy: 0,
        geo: [0, 0]
    });

    const setGeo = ({ lat, lng, address }: any) => {
        setGarage({ ...garage, geo: [lat, lng], address });
    }

    const setInfo = (info: any) => {
        setGarage({ ...garage, ...info });
        forward()
    }

    const submit = async () => {
        await addGarage(garage);
        refresh();
        close();
    }
    const forward = () => {
        if (state.step < InputComponents.length) {
            setState({ step: state.step + 1, completed: false });
        }
    }

    const InputComponents = [
        <SimpleMap forward={forward} setGeo={setGeo} />,
        <GarageInfoForm submit={setInfo} tables={tables} />,
        <CreateGarage submit={submit} />
    ]

    return (
        <Grid customCols="6fr 7fr" width="100%" height="100%" gridGap="0">
            <div className="grid" style={{ background: "#329181", height: "100%", padding: "1em" }}>
                <img src={car} style={{ maxWidth: "100%" }} />
            </div>
            {InputComponents[state.step]}
        </Grid>);
}
