import { GarageOccupancyTableItem as Item, GarageOccupancyTable } from '../../../model/domain/garage';

export const format = (aTable: GarageOccupancyTable) => {
    if (!aTable?.table)
        return [];

    const temp = new Array(24);
    Object.keys(aTable.table)
        .map((key: string) => {
            const items = aTable.table[key as 'sun'];
            items.map((item: Item, i: number) => {
                temp[i] = { ...temp[i], ...{ [key]: item } }
            });;
        })
    return temp;
}