import { addTimezoneChangeListener, removeTimezoneChangeListener, getTimezone  } from '../utils/time';
import { useEffect, useState } from 'react';

const useTimezone = () => {
  const [tz, setTz] = useState(getTimezone());

  useEffect(() => {
    const listener = (newTz: string) => {
      setTz(newTz);
    }

    addTimezoneChangeListener(listener);

    return () => removeTimezoneChangeListener(listener);
  },[]);

  return tz;
}

export default useTimezone;