import React from 'react'
import { TimeSeries } from '../../model/domain/metrics';
import { BarStack } from '@vx/shape';
import {  scaleOrdinal, scaleBand } from '@vx/scale';
import { colors } from './colors.json';
import { Group } from '@vx/group';
import { getColor } from './utils/color';

export interface GraphEvent {
    onTouchStart : (e : any) => void;
    onTouchMove : (e : any) => void;
    onMouseMove : (e : any) => void;
    onMouseLeave : (e : any) => void;
};
export type StackedAreasProps = {
    isSelected?: any;
    yScale: any;
    width : any;
    xScale: any;
    height: number;
    points: any;
    timeSeriesResult: TimeSeries.Result;
    event : (i: number ) => GraphEvent
};

export function StackedBar({ yScale, event, width, points}: StackedAreasProps) {
  const keys = Object.keys(points[0] || []).filter(k => k !== 'date') as any[];
  const getDate = (d: any) => (new Date(d?.date)).toISOString();;
  
  const getDates = (points: any[])=>{
      const dates = []
      for(let i = points.length - 1; i >= 0; i--){
        dates.push(getDate(points[i]))
      }
      return dates;
  }
  const xScale = scaleBand<string>({
    domain: getDates(points),
    range: [0, width],
    padding: 0.2
  });;

  const colorScale = scaleOrdinal<string, string>({
    domain: keys,
    range: colors,
  });

  return (
    <Group >
    <BarStack<any, string>
      keys={keys}
      data={points}
      xScale={xScale}
      yScale={yScale}
      color={colorScale}
      x={getDate}
    >
      {barStacks =>
        barStacks.map(barStack =>{
         const index = barStack.index;
         return barStack.bars.map((bar) =>(
            <rect
              key={`bar-stack-${barStack.index}-${bar.index}`}
              x={bar.x}
              y={bar.y}
              opacity={0.6}
              height={bar.height}
              width={bar.width}
              fill={getColor(index)}
            />
          ))}
        )}
    </BarStack>
    </Group>);
}


export default StackedBar;