import React, { useState, useEffect } from 'react';
import { Grid,  Card, Empty, Loader } from '../../../style';
import { primaryColor } from '../../../config';
import { useSelector } from 'react-redux';
import { Table } from 'antd';
import columns from './columns';
import { useOccupanyTable } from '../../../hooks/occupancy-table';
import TableList from './table-list';
import { format } from './format';

type Days = 'sun' | 'mon' | 'tue' | 'wed' | 'thur' | 'fri' | 'sat';

const OccupancyTable = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { tables, loading: loadingTables, addTable, editTable, removeTable } = useOccupanyTable({ eId })
    const [ selected, setSelected ] = useState(0);
    const localeProps = {
        locale: {
            emptyText: <Empty text="No Occupancy Table" />,
        },
        loading :{
            spinning: loadingTables,
            indicator: <Grid><Loader.Spinner size={2} color={primaryColor} /></Grid>
        }
    }
    useEffect(() => setSelected(0), [tables]);
    return (
        <Grid placeItems="start stretch" padding="1em" height="100%">
            <Grid customCols="auto 1fr" height="100%" placeItems="stretch">
                <TableList
                    add={addTable}
                    localeProps={localeProps}
                    tables={tables}
                    remove={removeTable}
                    loading={loadingTables}
                    edit={editTable}
                    select={setSelected}
                    selected={selected}
                />
                <Card shadow width="100%" height="100%">
                    <Table
                        bordered
                        {...localeProps}
                        pagination={false}
                        key={Math.random() * 1000}
                        style={{ color: primaryColor, maxWidth: "90vw" , minHeight: "800px"}}
                        dataSource={format(tables[selected] || [])}
                        columns={columns()}
                    />
                </Card>
            </Grid>
        </Grid>
    );
}

export default OccupancyTable;
