import { Space } from 'antd';
import { Button, Badge } from '../../style';
import React from 'react';
import { Popconfirm } from 'antd';
import { moment, FORMAT_DATETIME, Moment } from '../../utils/time';
import { overflow } from '../../utils/text';

const columns = (edit: any, remove: any, loadingAction = false, index = 0) => {
    return [{
        title: 'Title',
        dataIndex: 'title',
        key: 'title',
        render: (text: string) => text,
    },
    {
        title: 'Message',
        dataIndex: 'body',
        key: 'body',
        render: (text: string)=><div style={{textOverflow: 'ellipsis', height: "1.5em", overflow:"hidden", whiteSpace: "nowrap"}}>{text}</div>
    },
    {
        title: 'Status',
        dataIndex: 'status',
        render: (text: string) => <Badge
            className="shadow txt-center"
            style={{ background: text === 'expired' ? "red" : "" }}
        >
            {text}
        </Badge>,
    },
    {
        title: 'Launch Date',
        key: 'launchAt',
        dataIndex: 'launchAt',
        render: (t: string) => moment(t).format(FORMAT_DATETIME)
    },
    {
        title: 'Expiry Date',
        key: 'expiresAt',
        dataIndex: 'expiresAt',
        render: (t: string | null) => t? moment(t).format(FORMAT_DATETIME): 'n/a'
    },
    {
        title: 'Action',
        key: 'action',
        render: (_: any, record: any) => {
            return (
                <Space size="middle">
                    <Button
                        shadow
                        disabled={loadingAction}
                        loading={loadingAction}
                        onClick={() => edit(record)}
                    >
                        <i className="fa fa-edit"></i> &nbsp;Edit
                    </Button>

                    <Popconfirm
                        okButtonProps={{size: 'middle'}}
                        cancelButtonProps={{size: 'middle'}}
                        title="Are you sure"
                        onConfirm={() => remove(record)}
                    >
                        <Button
                            shadow
                            bg="red"
                            disabled={loadingAction}
                            loading={loadingAction}
                        >
                            <i className="fa fa-trash"></i> &nbsp;Delete
                        </Button>
                    </Popconfirm>
                </Space>
            )
        },
    },
    ]
};

export default columns;