import { getColor } from "../utils/color";
import { localPoint } from '@vx/event';
import { moment, Moment } from '../../../utils/time';
import { useTooltip  } from '@vx/tooltip';

interface TooltipData {
    timestamp: Moment;
    legend: { label: string, value: string, color: string }[];
  }

export default ({
    timeSeriesResult,
    metricDescriptor,
    setSelected,
    xScale
}: any) => {

    const tooltip = useTooltip<TooltipData>();
    const bisect = (t: Moment) => {
        let points = timeSeriesResult.timeSeries[0].points;
        for (let i = points.length - 1; i >= 0; i--) {
            const at = points[i].at;
            if (t.isSameOrBefore(at))
                return i;
        }
        return 0;
    }

    const getLegend = (i: number) => {
        const timeSeries = timeSeriesResult.timeSeries;
        const toPercent = (n: number) => (n * 100).toFixed(2) + '%';
        return timeSeries.reduce((acc: any, ts: any, index: any) => {
            const p = ts.points[i];

                const label = timeSeriesResult.crossSeriesReducer === 'NONE'
                    ? ts.displayLabel
                    : metricDescriptor.displayName;

                const value = timeSeriesResult.unit === '%' ? toPercent(p.value) : p.value + '';
                const temp = { label, value, color: getColor(index), isMissing: p.isMissing}
                acc.push(temp as never);
        
            return acc;
        }, []);
    }

    return {
        ...tooltip,
        handleTooltip: (event: any, newSelected = -1) => {
            const { x, y } = localPoint(event) || {};
            setSelected(newSelected)
            const timestamp = moment(xScale.invert(x || 0));
            const i = bisect(timestamp);
            const legend = getLegend(i);
            tooltip.showTooltip({
                tooltipData: { timestamp, legend },
                tooltipLeft: x,
                tooltipTop: y,
              });
          
        }
    }
}