export const useSize = ({height, width}: { height: number; width: number; }) =>{
    const yAxisRect = { left: 0, top: 0, height, width: 35 };
    const p = 10;
  
    const xAxisRect = {
      left: yAxisRect.width,
      top: height - 30,
      height: 30,
      width: width - yAxisRect.width - p
    };
  
    const drawAreaRect = {
      left: yAxisRect.width,
      top: 0,
      height: height - xAxisRect.height,
      width: width - yAxisRect.width - p
    }
  
    return {
      yAxis: yAxisRect,
      xAxis: xAxisRect,
      drawArea: drawAreaRect
    };
  }