import React, { useState } from 'react';
import { Grid, Button, Text } from '../../style';
import { Input, DatePicker } from 'antd';
import { useFormik } from 'formik';
import { moment, FORMAT_DATETIME, Moment } from '../../utils/time';

const nowISO = moment().toISOString();

const defaultValues = { title: "", body: "", status: "active", launchAt: nowISO, expiresAt: null };

const format = FORMAT_DATETIME;
const Form = ({ add, close, initialValues = defaultValues }: any) => {
    const [loading, setLoading] = useState(false);
    const { handleSubmit, handleChange, values, setFieldValue  } = useFormik({
        initialValues,
        onSubmit(values) {
            submit(values)
        }
    })
    const submit = async (values: any) => {
        try {
            setLoading(true)
            await add(values);
            close();
        } catch (e) {
        } finally {
            setLoading(false)
        }
    }

    const error = !values.launchAt || !values.body || !values.title
    const style = {
        padding: ".5em 1em", height: "100%", width: "100%",
        placeItems: "center stretch", gridGap: ".4em", background: "white"
    }

    return (<form onSubmit={handleSubmit} className="grid" style={style}>
        <div>
            <Text textAlign="left" bold>Annoucement Title</Text>
            <Input
                name={"title"}
                value={values.title}
                onChange={handleChange}
                placeholder="Example: SJSU Graduation Ceramony"
            />
        </div>

        <div>
            <Text textAlign="left" bold>Annoucement Message</Text>
            <Input.TextArea  name={"body"} value={values.body}
                onChange={handleChange}
                placeholder="Example: Information on the Event" />
        </div>

        <div className="grid" style={{placeItems: "end" }}>
            <Text textAlign="left" placeSelf="stretch" bold>Message Launch Date</Text>
            <DatePicker
                use12Hours
                showTime={{format: 'hh:mm a'}}
                format={format}
                minuteStep={15}
                style={{width: "100%"}}
                value={moment(values.launchAt)}
                onChange={(e: Moment | null) => { 
                  if (!e) return;
                  setFieldValue("launchAt", e.toISOString());
                }} />
        </div>

        <div className="grid" style={{placeItems: "end" }}>
            <Text textAlign="left" placeSelf="stretch" bold>Message Expiry Date</Text>
            <DatePicker
                use12Hours
                style={{width: "100%"}}
                showTime={{format: 'hh:mm a'}}
                format={format}
                minuteStep={15}
                value={values.expiresAt? moment(values.expiresAt): null}
                onChange={(e: Moment | null) => setFieldValue("expiresAt", e?.toISOString() || null)} />
        </div>

        <Grid cols="2" size="0.7em" placeItems={"stretch"}>
            <Button.Normal
                type="submit"
                disabled={loading}
                onClick={() => close()}
            >
                Cancel
                </Button.Normal>
            {loading ? 
            <Button loading disabled>Loading</Button> :
                <Button.Normal
                    type="submit"
                    disabled={error}
                    onSubmit={(e: any) => handleSubmit(e)}>
                    Confirm
            </Button.Normal>
            }
        </Grid>
    </form>
    )
}

export default Form;
