import { useReducer } from "react";
import { TimeSeries } from "../model/domain/metrics";
import { moment, defaultInterval } from '../utils/time';

export const defaultTimeSeries: TimeSeries.Query = {
  metricType: "garage_occupancy_count",
  resourceIds: [],
  crossSeriesReducer: "NONE",
  alignment: null,
  interval: defaultInterval()
};

export enum actions {
  SET_GARAGES,
  SET_ALIGNMENT,
  SET_METRICTYPE,
  SET_INTERVAL
};

export function reducer(state: TimeSeries.Query, action: any) {
  const { startTime: start, endTime: end } = state.interval;
  const setTimeseries = (props: any) => ({ ...state, ...props });

  const getInterval = (props : any) => {
    let { startTime = start, endTime = end } = props;
    console.log(props)
    startTime = moment(startTime);
    endTime = moment(endTime);
    const shouldSwap = startTime > endTime;
    return {
      startTime: (shouldSwap ? endTime : startTime).toISOString(),
      endTime: (shouldSwap ? startTime : endTime).toISOString()
    };
  };

  switch (action.type) {
    case actions.SET_METRICTYPE:
      return setTimeseries({ metricType: action.payload.metricType });

    case actions.SET_GARAGES:
      return setTimeseries({ resourceIds: action.payload.resourceIds });

    case actions.SET_INTERVAL:
      return setTimeseries({ interval: getInterval(action.payload.interval) });

    case actions.SET_ALIGNMENT:
      const alignment = action.payload || null;
      return setTimeseries({ alignment });

    default:
      return state;
  }
}

const useTimeseries = (initialState = defaultTimeSeries) => {
  const [timeseries, dispatch] = useReducer(reducer, initialState);
  const setGarages = (resourceIds: string[]) => dispatch({ type: actions.SET_GARAGES, payload: { resourceIds } });

  const setMetric = (metricType: string) => dispatch({ type: actions.SET_METRICTYPE, payload: { metricType } });
  const setAlignment = (e: any) => dispatch({ type: actions.SET_ALIGNMENT, payload: e })
  const setInterval = (interval: any) => dispatch({ type: actions.SET_INTERVAL, payload: { interval } })
  return {
    timeseries,
    actions: {
      setGarages,
      setAlignment,
      setMetric,
      setInterval
    }
  }
}

export default useTimeseries;