import React, { useState } from 'react';
import { Grid, Switch } from '../../style';
import Layout from '../../layout';
import Listings from './listings';
import Analytics from './analytics';
import OccupancyTable from './occupancy-table';
import Header from "./header";

const activePages = {
  "ANALYTICS": "ANALYTICS",
  "LISTINGS": "LISTINGS",
  "OCCUPANCY": "OCCUPANCY"
};

const Garage = () => {
  const [activePage, setActivePage] = useState(activePages.ANALYTICS);
  const [open, setOpen] = useState(true);
  const shouldPad = open && activePage === activePages.ANALYTICS && window.innerWidth > 1360;
  const filterWidth = open ? 375 : 0;
  return (
    <Layout sidebar>
      <Grid customCols="1fr auto" width="100%" gridGap="0" placeItems="start" style={{ height: "100%" }}>
        <div style={{ width: "100%", placeSelf: 'start end' }}>
          <Layout.Top style={shouldPad ? { margin: '0 0 0 8%', maxWidth: '100%' } : {}}
          >
            <Switch.underlined
              style={{ placeSelf: "start", gridColumn: '1/-1' }}
              items={Object.keys(activePages)}
              onClick={(active: string) => setActivePage(active)}
              active={activePage}
            />
            <Header />
          </Layout.Top>
          <Layout.Bottom style={shouldPad ?
              { margin: '-10em 0 0 8%', maxWidth: '100%' } :
              { margin: '-10em auto auto auto', maxWidth: '1500px' }}
          >
            {activePage === activePages.LISTINGS ? <Listings /> :
              activePage === activePages.OCCUPANCY ? <OccupancyTable />
                : <Analytics filterWidth={filterWidth} open={open} setOpen={setOpen} />
            }
          </Layout.Bottom>
        </div>
        {activePage === activePages.ANALYTICS && <div className='overlay-tablet' style={{ width: filterWidth }}></div>}
      </Grid>
    </Layout>
  );
}

export default Garage;