import { Charts } from "..";
import { scaleTime, scaleLinear } from '@vx/scale';
import { TimeSeries } from '../../../model/domain/metrics';
import { moment, getTimezone } from '../../../utils/time'
import { ScaleTime, ScaleLinear, ScaleBand } from 'd3-scale';

interface XScale {
    scale: ScaleTime<number, number> | ScaleBand<any>;
    tickFormat: (date: Date) => string;
}
  
interface YScale {
    scale: ScaleLinear<number, number>;
    tickFormat: (n: number) => string;
}

type Scale =  { chart: Charts, yMax: number, height: number, width: number,  timeSeriesResult: TimeSeries.Result, points : any };

export const useScale = ({ chart,  height, width, timeSeriesResult, points, yMax }: Scale) => {
  const xScale = getXScale(width, timeSeriesResult, points, chart);
  const yScale = getYScale(height, timeSeriesResult, yMax);

    return {
        scale: {
            x: xScale.scale,
            y: yScale.scale
        },
        tickFormat: {
            x: xScale.tickFormat,
            y: yScale.tickFormat
        }
    }

};
  
function getXScale(width: number, timeSeriesResult: TimeSeries.Result, points : any, chart: Charts): XScale {
    const { startTime, endTime } = timeSeriesResult.interval;
    const getDate = (d: any) => (new Date(d?.date)).toISOString();

    
    const [s, e] = [moment(startTime), moment(endTime)];
    const scale = scaleTime({
    domain: [s, e],
    range: [0, width]
  })
    
    const tickFormat = (() => {
      const dayDiff = e.diff(s, 'days', true);
      const monthDiff = e.diff(s, 'months', true);
      const formatString = dayDiff <= 1
        ? 'h:mm A'
        : dayDiff <= 7
        ? 'ddd h A'
        : monthDiff <= 6
        ? 'MMM DD, h A'
        : 'MMM';
  
      return (date: Date) => {
        const m = moment.tz(date.toISOString(), getTimezone());
        return m.format(formatString);
      }
    })();
  
    return { scale, tickFormat };
  }
  
 function getYScale(height: number, timeSeriesResult: TimeSeries.Result, yMax: number): YScale {
    const scale = scaleLinear({
      domain : [0,  1.10 * yMax],
      range: [height, 0]
    } as any);
  
    const tickFormat = (() => {
      return (n: number) => {
        if (timeSeriesResult.unit !== '%') 
          return n + '';  
        return Math.round(n * 100) + '%';
      }
    })();
  
    return { scale, tickFormat } as any;
  }
  