import { User } from "../../model/domain/user";
import Key from "../../json/keys.json";
import userStates from "../../json/user-state.json";

interface UserActions {
  type: ActionTypes;
  user?: User;
  enterprise?: any,
  token?: string;
  loading: boolean;
  error: boolean;
  userState: "APPROVED" | "UNAUTH" | "ENTERPISE_SELECT" | "ENTERPISE_PENDING";
}

const intialState = {
  user: {},
  enterprise: {},
  error: false,
  loading: false,
  userState: userStates.UNAUTH
};

export enum ActionTypes {
  LOGIN = "LOGIN",
  AUTH = "AUTH",
  LOGOUT = "LOGOUT",
  SIGNUP = "SIGNUP",
  ADD_ENTERPISE = "ADD_ENTERPISE",
  PASSWORD_RESET = "PASSWORD_RESET",
  FORGOT_PASSWORD = "FORGOT_PASSWORD",
  GET_ENTERPRISE = "GET_ENTERPRISE",
  USER_REQUEST_FAILED = "USER_REQUEST_FAILED",
  USER_REQUEST_PENDING = "USER_REQUEST_PENDING",
  USER_REQUEST_SUCCESS = "USER_REQUEST_SUCCESS",
  RESET_ERROR = "RESET_ERROR"
};

const calcUserState = (user: User | undefined) => {
  if (!user || user === undefined||  user.apps === undefined) return userStates.UNAUTH;
  else if (user.apps.enterpriseApp === "onboardRequestRequired")
    return userStates.ENTERPISE_SELECT;
  else if (user.apps.enterpriseApp === "onboarding")
    return userStates.ENTERPISE_PENDING;
  return userStates.APPROVED;
};

export default function rootReducer(
  state = intialState,
  action: UserActions
): any {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      localStorage.removeItem(Key.USER);
      return { ...state, user: undefined, userState: userStates.UNAUTH , loading: false};

    case ActionTypes.LOGIN:
    case ActionTypes.SIGNUP:
      const { user, token } = action;
      localStorage.setItem(Key.USER, token || "");
      return { ...state, user, userState: calcUserState(user), loading: false };

    case ActionTypes.AUTH:
      return { ...state, user: action.user, userState: calcUserState(action.user), loading: false };

    case ActionTypes.ADD_ENTERPISE:
      return { ...state, userState: userStates.ENTERPISE_PENDING };
    
      case ActionTypes.GET_ENTERPRISE:
        return { ...state, enterprise: action.enterprise };
      
    case ActionTypes.USER_REQUEST_PENDING:
      return { ...state, loading: true, error: false };

    case ActionTypes.USER_REQUEST_FAILED:
      return { ...state, error: true, loading: false };

    case ActionTypes.USER_REQUEST_SUCCESS:
      return { ...state, loading: false };

    case ActionTypes.RESET_ERROR:
      return { ...state, error: false };

    default:
      return state;
  }
}
