import React from 'react';
import styled from 'styled-components';

const ImgBox = styled.img`
    object-fit: cover;
    width: 8em;
    padding: .5em;
    height: 8em;
`;

export const Avatar = ({ url, src, large, style, medium }: any) => {
    style = {
        ...style, ...large? {
            borderWidth: '4px',
            width: '15em',
            maxHeight: "90vw",
            height: '15em'
        } : !medium? {
            width: '3em',
            height: '3em'
        }: {}
    };
  return (<ImgBox src={url || src} style={style} />);
}

export default Avatar;