import React, { useEffect } from "react";
import { Card, Empty, Button, Grid} from "../../style";
import { Table } from "antd";
import { primaryColor, mobileWidth } from "../../config";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import { getAnnouncements, deleteAnnouncement } from "../../store/actions/announcement";
import columns from "./columns";

let locale = {
  emptyText: <Empty text="No Annoucement" />
};

export default ({ announcements, edit, add, toggler }: any) => {
  const eId = useSelector((state: any) => state.user.enterprise.id);
  const dispatch = useDispatch();
  const loadingAction = useSelector(
    (state: any) => state.announcement.loadingAction
  );
  const loadingAnnouncements = useSelector(
    (state: any) => state.announcement.loadingAnnouncements
  );

  useEffect(() => {
    if (eId) dispatch(getAnnouncements(eId));
  }, [eId])

  return (
    <Card shadow minWidth="60vw" padding=".5em">
      <Grid customCols="auto 1fr auto">
        <div></div>
        {toggler}
        <Button
          rounded
          invert
          padding=".2em"
          shadow
          faIcon={`fa fa-${"plus"}`}
          onClick={add}
          minWidth="4.5em"
        > Add
        </Button>

      </Grid>
      <DIV>
        <Table
          locale={locale}
          key={Math.random() * 1000}
          loading={loadingAnnouncements}
          style={{ color: primaryColor, maxWidth: "90vw" }}
          dataSource={announcements}
          columns={columns(edit,
            (i: any) =>
              dispatch(deleteAnnouncement(eId, i)), loadingAction
          )}
          summary={(pageData) => <Table.Summary.Row
            key={Math.random() * 1000}
            className="bold"
            style={{ color: primaryColor }}
          >
            <br />
                Total Annoucements: {pageData.length}
          </Table.Summary.Row>
          }
        />
      </DIV>
    </Card>
  );
};

const DIV = styled.div`
  @media (max-width: ${mobileWidth}) {
    overflow-x: scroll;
  }
`;