import React from 'react'
import { TimeSeries } from '../../model/domain/metrics';
import { colors } from './colors.json';
import { curveBasis } from 'd3-shape';
import { LinePath } from '@vx/shape';
import moment from 'moment-timezone';

const reducer = (datum: TimeSeries.Point): boolean => !datum.isMissing;
export default function MultiLine({ points, isSelected, event, xScale, yScale, width}: any) {
  const getColor = (index: number) => colors[index % colors.length];
  const linePathX = (d: TimeSeries.Point) => xScale(moment(d.at));
  const linePathY = (d: TimeSeries.Point) =>  yScale(d.value);
  
  return (
  <>
  {points?.map((data: any, index : number) => (
        <LinePath<TimeSeries.Point>
          x={linePathX}
          y={linePathY}
          {...event(index)}
          data={data}
          key={index}
          defined={reducer}
          strokeWidth={isSelected(index)? 4: 2}
          stroke={getColor(index)}
          curve={curveBasis}
        />
      )
    )}
  </>);
}