import React from 'react';
import {  Header, Card, Button,  Modal } from '../../../style';
import { GarageOccupancyTableItem as Item, GarageOccupancyTable } from '../../../model/domain/garage';
import { List  } from 'antd';
import { defaultTable } from './default-table';
import OccupancyForm from './table-form';
import TableItem from './table-item';

const TableList = ({ add, remove, selected, select, edit, tables, loading, localeProps } : any) => {
    const AddModal = Modal({
        title: "Add Your Occupancy Table",
        message: 
        <OccupancyForm
            action={add}
            close={() => AddModal.close()}
            initialValues={defaultTable}
        />,
        hideCancel: true,
    });

    const open = (selected: GarageOccupancyTable) => {
        const EditModal =  Modal({
            title: "Edit Your Occupancy Table",
            message: <OccupancyForm
                action={edit}
                initialValues={selected}
                close={() => EditModal.close()}
            />,
            hideCancel: true,
        });
        EditModal.open();
    }

    return (
        <Card shadow width="400px" grid customRows="auto 1fr" placeItems="stretch">
            <Header bold width="100%" size={3}>
                Occupancy Tables
                <Button
                    style={{ placeSelf: "end", fontSize: ".6em", float: "right" }}
                    rounded
                    shadow
                    onClick={AddModal.open}
                    minWidth="4.5em"
                >
                    <i className={`fa fa-${"plus"}`}></i> Add
                </Button>
            </Header>
            <List
                {...localeProps}
                bordered={!loading}
                dataSource={tables}
                renderItem={(table: GarageOccupancyTable, i: number) => (
                    <TableItem
                        select={() => select(i)}
                        active={i === selected}
                        table={table}
                        remove={remove}
                        open={() => open(tables[i])}
                    />
                )}
            />
        </Card>
    );
}

export default TableList;