import React, { useRef} from 'react';
import { Grid,  Button, Text } from '../../../style';
import { primaryColor } from '../../../config';
import { GarageOccupancyTableItem as Item, GarageOccupancyTable } from '../../../model/domain/garage';
import { Table,  Input  } from 'antd';
import columns from './columns';
import { format } from './format';

type Days = 'sun' | 'mon' | 'tue' | 'wed' | 'thur' | 'fri' | 'sat';

const OccupancyForm = (props: any) => {
    const { action, close } = props;
    const initialValues: GarageOccupancyTable = props.initialValues;
    const input = useRef<any>(null)
    const days = Object.keys(initialValues.table) as Days[];

    const submit = () => {
        action({
            title: getTitle(),
            id: initialValues.id,
            table: getTable(days)
        });
        close();
    }

    const getTitle = () => {
        return input?.current?.state?.value || initialValues.title
    }
    const getTable = (days: Days[]) => (days
        .reduce((table: any, day: Days) => {
            const values = getOccupanciesByDay(day);
            const intervals = initialValues.table[day];
            const newIntervals = intervals.map(({ interval }: Item, i: number) => {
                return { interval, occupancy: values[i] }
            })

            table[day] = newIntervals;
            return table;
        }, {})
    )

    const toArray = (elements: HTMLCollectionOf<Element>): Element[] => {
        const items = []
        for (let i = 0; i < elements.length; i++) {
            items.push(elements[i])
        }
        return items;
    }

    const getOccupanciesByDay = (day: Days): Number[] => {
        const items = toArray(document.getElementsByClassName(day));
        items.sort((a: Element, b: Element) => Number(a.classList[1]) - Number(b.classList));
        return items
            .map((item: Element) => {
                const secondChild = item?.children[1] as any;
                const value = secondChild?.children[0]?.value || '0';
                return Number(value) / 100
            })
    }
    return (
        <Grid placeItems="stretch" width="100%" height="100%" gridGap="0" background="white" padding=".5em .8em 1em .8em" >

            <div style={{ padding: ".5em" }}>
                <Text textAlign="left" bold>Enter the Name of your Table</Text>
                <Input
                    placeholder={"Ex: New Table 1"}
                    name={"title"}
                    ref={input}
                    defaultValue={initialValues.title}
                />
            </div>
            <div style={{ overflowY: "scroll", maxHeight: "75vh" }}>
                <Table
                    bordered
                    pagination={false}
                    key={Math.random() * 1000}
                    style={{ color: primaryColor, maxWidth: "100%" }}
                    dataSource={format(initialValues)}
                    columns={columns(true, 0)}
                />
            </div>
            <div>
                <Button onClick={close}>Cancel</Button>
                <Button onClick={() => { submit() }}>Submit</Button>
            </div>
        </Grid>
    );
};

export default OccupancyForm;