import React from 'react';
import empty from "../../assets/empty.svg";
import { Grid, Header } from '../';

const Empty = ({ text = 'No Data', style = {}, color ="inherit"})=>{
return(<span>
      <Grid>
      <img src={empty} style={{ maxWidth: "40vw", width: "200px", minHeight: "200px", marginTop: "10vh", ...style }} />
        <Header bold color={color}>{text}</Header>
      </Grid>
    </span>
  )
};

export default Empty;