import React from 'react';
import Layout from '../../layout';
import img from '../../assets/profilee.svg' ;
import { Grid } from '../../style';

const Profile = ()=>{
    return(
    <Layout sidebar>
        <Grid padding="6em 2em">
        <img src={img} style={{maxWidth: "80vw"}} alt="profile"/>
        </Grid>
    </Layout>);
}

export default Profile;