import React from 'react';
import { Grid, Header as Text, List } from '../../style';
import useGarages from '../../hooks/garage';
import { useSelector } from 'react-redux';
import { Garage } from '../../model/domain/garage';
import { ScoreCard } from '../../components/score-card/score-card';
import { overflow } from '../../utils/text';

const Header = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { garages, loading } = useGarages({ eId });
    const activeGarages = garages
        .filter((garage: Garage) => garage.active && garage.occupancy?.count !== undefined)
        .sort((a, b) => (a.occupancy?.count || 0) - (b.occupancy?.count || 0));

    const totalOcc = activeGarages.reduce(((acc, { maxOccupancy }: Garage) => acc + maxOccupancy), 0);
    const currentOcc = activeGarages.reduce(((acc, { occupancy }: Garage) => acc + (occupancy?.count || 0)), 0);

    const percent = (curr: number, total: number) => (((curr / total) * 100) || 0).toFixed(2);
    return (
        <Grid
            cols='5'
            style={{ gridColumn: '1/-1' }}
            width='100%'
            padding='1.5em 1.5em'
        >
            <ScoreCard title='All Parked Vehicles' faIcon='fa fa-car' label='Parked Vehicles' borderRight>
                <span className='txt-lgx'>{currentOcc}</span>
                <span className='txt-md'>/{totalOcc}</span>
            </ScoreCard>

            <ScoreCard title='All Parked Vehicles(%)' faIcon='fa fa-car' label='Parked Vehicles' borderRight>
                <ScoreCard.PercentBar
                    type='dashboard'
                    percent={percent(currentOcc, totalOcc)}
                />
            </ScoreCard>

            <ScoreCard title='Active Garages' faIcon='fa fa-car' label='Parked Vehicles' borderRight>
                <ScoreCard.PercentBar
                    type='dashboard'
                    text={`${activeGarages.length}/${garages.length}`}
                    percent={percent(activeGarages.length, garages.length)}
                />
            </ScoreCard>

            <ScoreCard title='Garage Occupancies' faIcon='fa fa-car' label='Parked Vehicles' span={2}>
                <Grid placeSelf='start' >
                    <Grid
                        customCols={loading ? '1fr' : '1fr  minmax(15em, auto)'}
                        placeItems='stretch'
                        width='100%'
                        color='white'
                        gridGap='.2em 1.5em'
                    >
                        <List
                            loading={loading}
                            pagination={{ items: 4 }}
                            list={activeGarages}
                            map={(({ name, occupancy, maxOccupancy }: Garage) => {
                                const { count } = occupancy || { count: 0 };
                                const value = percent(count, maxOccupancy)
                                return (
                                    <>
                                        <Text size={3.75} textAlign='left' color={'white'}>{overflow(name, 20)}</Text>
                                        <ScoreCard.PercentBar
                                            status
                                            percent={value}
                                            text={count ? value + "%" : 'N/A'}
                                        />
                                    </>
                                )
                            })}
                        />
                    </Grid>
                </Grid>
            </ScoreCard>
        </Grid>);
}

export default Header;