import { useMemo } from "react";
import { moment } from '../../../utils/time';
import { TimeSeries } from "../../../model/domain/metrics";

export const useStackedData = ( timeSeriesResult : any, reverse = false) => useMemo(() => {
    const points : any[]= [];
    let max = 0;
    const{ timeSeries } = timeSeriesResult
    const reference = timeSeries[0]
    const numPoints = reference.points.length;
    const numResource = timeSeries.length
   
    for(let i = 0; i < numPoints; i++){ 
      let point = { date: reference.points[i].at };
      let totalValue = 0;

      for(let j = 0; j < numResource; j++){
        const resource = timeSeries[j].displayLabel;
        const value = timeSeries[j].points[i].value;
         totalValue += value > 0? value: 0;
        point = {
          ...point,
          [resource] : String(value === -1? 0 : value)
        }
      }
      points.push(point);
      max = max < totalValue? totalValue : max;
    }

    return {
        points,
        max
    };
  }, [timeSeriesResult]);

export const useLinearData = ( timeSeriesResult : TimeSeries.Result) =>  useMemo(() => {
  const k = timeSeriesResult.alignmentPeriod;
  const points = timeSeriesResult.timeSeries.map((ts: any) => {
    const pairedPoints = ts.points.reduce((pairedPoints: any, point: any) => {
      const pointPair = {
        value: point.value,
        isMissing: point.isMissing,
        at: moment(point.at).subtract(k, 'minutes')
      }

      pairedPoints.push(point);
      pairedPoints.push(pointPair);
      return pairedPoints;
    }, []);

    return pairedPoints;
  });
  return { points, max : timeSeriesResult.max };
}
  , [timeSeriesResult]);