  
import React from 'react';
import { ComponentProvider } from './context/components';
import {NavProvider } from './context/nav-context';
import { Provider } from 'react-redux';
import store from './store'
import { createGlobalStyle } from "styled-components";
import { primaryColor } from './config';
import './style/main.scss';
import 'antd/dist/antd.css';

const GlobalStyle = createGlobalStyle`
  html,body{
    font-size: 15px;
    font-family: 'Roboto', sans-serif;
  }
  .ant-btn-primary, .ant-btn{
    border-radius: .3em;
    box-shadow: 0 .1em .1em rgba(0,0,0,.2);
  }
  .ant-btn-primary {
    border-color: ${primaryColor};
    background: ${primaryColor};
  }
  .ant-pagination-item, .ant-pagination-item-active a, .ant-select-selector:hover{
    border-radius: .3em;
    color: black !important;
    border-color: ${primaryColor} !important;
  }
  .ant-select-selector{
    border-radius: .4em !important;
  }
  .ant-picker {
    border-color: #d9d9d9;
  }
  .ant-select-show-search.ant-select-multiple .ant-select-selector{
    height: 40px;
    overflow-y: auto;
  }

  input:focus, textarea:focus, .ant-picker-focused, input:hover, textarea:hover, .ant-picker:hover{
    border-color: ${primaryColor} !important;
  }
  .ant-btn-primary:hover, .ant-btn-primary:focus, .ant-btn-primary:active, .ant-btn:hover, .ant-btn:focus, .ant-btn:active {
    color: ${primaryColor} !important;
    border-color: ${primaryColor};
    background: white;
    transition: 200ms ease;
    box-shadow: 0 0 0;
  }
  
  .ant-table-cell{
    max-width: 300px;
    font-size: 15px;
  }`;

const Main = ({ children }: any) => (
    <NavProvider>
  <ComponentProvider>
  <Provider store={store}>
          <GlobalStyle />
         {children} 
    </Provider>
   </ComponentProvider>
</NavProvider>
);

export default Main;