import {  InputNumber } from "antd";
import React from "react";
import { GarageOccupancyTableItem as Item } from "../../../model/domain/garage";

const days = [
   {
    title: "Time",
    dataIndex: "sun",
   },
  {
    title: "Sunday",
    dataIndex: "sun",
  },
  {
    title: "Monday",
    dataIndex: "mon",
  },
  {
    title: "Tuesday",
    dataIndex: "tue",
  },
  {
    title: "Wednesday",
    dataIndex: "wed",
  },
  {
    title: "Thrusday",
    dataIndex: "thur",
  },
  {
    title: "Friday",
    dataIndex: "fri",
  },
  {
    title: "Saturday",
    dataIndex: "sat",
  },
];

type Interval = { startHour: [number, number], endHour: [number, number ] }

const getTime = (range : Interval)=>{
    const [hour1, min1 ] = range.startHour;
    const [hour2, min2 ] = range.endHour;
    const period = hour1 >= 12? 'PM' : 'AM'
    return `${format(hour1)}:${pad(min1)+ period} -\n ${format(hour2)}:${pad(min2) + period}`
}

const pad = (digit: number) => String(digit < 10? `0${digit}` : digit);

const to12hours = (hour : number) => Math.abs((hour - 12) !== 0? (hour - 12) : 12);

const format = (hour : number) => pad(to12hours(hour));

const columns = ( edit = false, index = 0, setFieldValue = (e : any,f: any, g: any)=>{}) => {
    return days.map(({title, dataIndex})=>{
        return {
            title : title !== 'Time'? title : <div>{title} &nbsp;&nbsp; <i className="fa fa-clock"></i></div>,
            dataIndex,
            key: dataIndex,
            render: (item: Item, _: any, i: number) =>{
                const handleChange = (e: any)=> setFieldValue(dataIndex, i, {...item, occupancy : Math.floor(e/100)})
                const occupancy = Math.floor(item.occupancy * 100);
                return (title !== 'Time'? 
                    edit? 
                        <InputNumber 
                            className={`${dataIndex} ${i}`}
                            min={0}
                            max={100}  
                            style={{placeSelf: "end", width: "60px"}}
                            defaultValue={occupancy}
                            onChange={handleChange} 
                            precision={0}
                            name={"maxOccupancy"} 
                        /> : 
                        `${occupancy}%` : 
                   <b>{getTime(item.interval)}</b>
                   );
          }
        }
    })
}

export default columns;
