import React from 'react'
import { Grid, List } from '../../style';
import { ScoreCard } from './score-card';

const ScoreCards = ({ style, scoreCards, loading }: any) => {
  return (
    <div className="slide-up"
      style={{ width: "100%", gridColumn: "1/-1", padding: ".9em .5em", ...style }}>
      <Grid cols={loading ? 1 : 5} placeItems={loading ? 'center' : 'stretch'} style={{ minHeight: "9em" }}>
        <List
          list={scoreCards}
          loading={loading}
          map={(item: any, index: number) => (
            <ScoreCard.FlatValue
              borderRight={index < scoreCards.length - 1}
              name={item.title}
              flatValueQuery={item.flatValueQuery}
            />
          )}
        />
      </Grid>
    </div>);
}

export default ScoreCards;