import { useState, useEffect } from "react";
import API from "../api";
import { TimeSeries } from "../model/domain/metrics";
import { moment, Moment, defaultInterval } from '../utils/time';

interface MetricDataStatus {
  error: boolean;
  loading: boolean;
  data: {
    timeSeriesResult: TimeSeries.Result | null;
    metricDescriptor: TimeSeries.MetricDescriptor;
  } | null;
}

export const useMetricData = ({ timeseries, interval = defaultInterval(), eId }: any) => {
  const [state, setState] = useState<TimeSeries.Query>({  interval, ...timeseries, });
  const {
    interval: { startTime, endTime },
  } = state;

  const [metricDataStatus, setMetricDataStatus] = useState<MetricDataStatus>({
    error: false,
    loading: false,
    data: null,
  });

  const setInterval = (interval: any) => setState({ ...state, interval });

  const setEndTime = (newEndtime: Moment | null) => {
    if (newEndtime) {
      setState({
        ...state,
        interval: getInterval(moment(startTime), newEndtime)
      });
    }
  };

  const setStartTime = (newStartTime: Moment | null) => {
    if (newStartTime) {
      setState({
        ...state,
        interval: getInterval(newStartTime, moment(endTime)),
      });
    }
  };

  const getInterval = (start: Moment, end: Moment) => {
    const shouldSwap = start > end;
    return {
      startTime: (shouldSwap ? end : start).toISOString(),
      endTime: (shouldSwap ? start : end).toISOString()
    };
  };

  const getMetricData = async (query: TimeSeries.Query) => {
    try {
      setMetricDataStatus({ data: null, loading: true, error: false });
      const res = await Promise.all(
        [API.getTimeSeries(eId, query), API.getTimeSeriesMetricDescriptor(eId, query.metricType)]);
        const [{ timeSeriesResult }, { metricDescriptor }] = res;
        console.log(metricDescriptor)
        console.log(timeSeriesResult)
      setMetricDataStatus({
        loading: false,
        error: false,
        data: {
          timeSeriesResult,
          metricDescriptor
        }
      });
    } catch (e) {
      setMetricDataStatus({
        loading: false,
        error: true,
        data: null,
      });
    }
  };

  useEffect(() => { getMetricData({ ...state }) }, [state]);
  return {
    metricDataStatus,
    interval: state.interval,
    setEndTime,
    setStartTime,
    setInterval,
    refresh: (d: any) => {
      const query = { ...{ interval: state.interval},  ...d};
      setState(query)
    }
  };
};

export default { useMetricData };