import React from 'react';
import { ScaleTime, ScaleLinear, ScaleBand} from 'd3-scale';
import { AxisBottom, AxisLeft } from '@vx/axis';

export const createXAxis = (() => {
  const getTickCount = (width: number) => width < 600? 5: 10;

  return (scale: ScaleTime<number, number>, axisProps?: any): JSX.Element => {
    const [, width] = scale.range();
    const tickCount = getTickCount(width);
    const tickValues = scale.ticks(tickCount);
    axisProps = axisProps || {};

    return (
      <AxisBottom 
        {...axisProps} 
        scale={scale} 
        tickValues={tickValues} 
    />
    );
  }
})();

export const createYAxis = (() => {
  const getTickCount = (height: number) =>  height < 500? 5: 10;
  return (scale: ScaleLinear<number, number>, axisProps?: any): JSX.Element => {
    const [height, ] = scale.range();
    const tickCount = getTickCount(height);
    const tickValues = scale.ticks(tickCount);
    axisProps = axisProps || {};
    
    return (
      <AxisLeft
        {...axisProps}
        scale={scale}
        tickValues={tickValues} 
      />
    );
  }
})();