import React from 'react'
import { ScaleTime, ScaleLinear } from 'd3-scale';
import { TimeSeries } from '../../model/domain/metrics';
import StackedArea from './stacked-area';
import StackedBar from './stacked-bar';
import useToolTip from './hooks/tooltip';
import Tooltip from './tooltip';
import MultiLine from './multi-line';
import { Charts } from '.';
import Grid from './grid';
import { useLinearData, useStackedData } from './hooks/data';

export interface DrawAreaProps {
  backgroundColor: string;
  width : number;
  height: number;
  setSelected? : any;
  noneSelected? : any;
  isSelected?: any;
  chart: any;
  setChart: any;
  timeSeriesResult: TimeSeries.Result;
  metricDescriptor: TimeSeries.MetricDescriptor;
  xScale: ScaleTime<number, number>;
  yScale: ScaleLinear<number, number>;
}

export function DrawArea({ width, height,chart, noneSelected, timeSeriesResult, metricDescriptor, xScale, yScale, setSelected, isSelected}: DrawAreaProps) {
  const { tooltipData, hideTooltip, handleTooltip, tooltipLeft, tooltipTop, tooltipOpen } = useToolTip({
    timeSeriesResult,
    metricDescriptor,
    setSelected,
    xScale
  });
  
  const { points: linearPoints }= useLinearData(timeSeriesResult);
  const {points: stackedPoints } = useStackedData(timeSeriesResult, chart === Charts.stackedArea);
  const points = chart === Charts.multiLine? linearPoints: stackedPoints;

  const event = (index = -1) => {
    return {
     onTouchStart : (e : any) => handleTooltip(e, index),
     onTouchMove : (e : any) => handleTooltip(e, index),
     onMouseMove : (e : any) => handleTooltip(e, index),
     onMouseLeave : () => {
       setSelected(-1)
       hideTooltip();
     }
    }
 }
  const chartProps = {
    yScale,
    width,
    height,
    points,
    xScale,
    isSelected,
    noneSelected,
    timeSeriesResult,
    event
  };

  return (
    <>
      <svg width={width} height={height}>
      <rect
          x={0}
          y={0}
          width={width}
          height={height}
          fill={'transparent'}
          {...event()}
        />    
        <Grid yScale={yScale} width={width} />
        {getChart(chart, chartProps)}
        {chart === Charts.stackedBar && <rect
          x={0}
          y={0}
          width={width}
          height={height}
          fill={'transparent'}
          {...event()}
        />  

        }
        <Tooltip.Line
          height={height} 
          tooltipData={tooltipData}
          tooltipLeft={tooltipLeft}
        />
      </svg>
      <Tooltip 
        tooltipOpen={tooltipTop} 
        tooltipLeft={tooltipLeft} 
        tooltipTop={tooltipOpen} 
        tooltipData={tooltipData} 
        isSelected={isSelected}
        noneSelected={noneSelected}
      />
    </>
  )
}
const getChart = (chart: Charts, props: any) => {
  switch (chart) {
    case Charts.multiLine:
      return <MultiLine {...props} />;

    case Charts.stackedArea:
      return <StackedArea {...props} />;

    default:
      return <StackedBar {...props} />;
  }
}