import React, { useState, useEffect } from "react";
import { Garage } from "../../../model/domain/garage";
import { Card, Header, Text, Grid, Badge } from "../../../style";
import { Progress } from "antd";
import { primaryColor } from "../../../config";
import { moment } from '../../../utils/time';
const GarageCard = (props: Garage & any) => {
  const { name, address, maxOccupancy, active, edit, occupancyUpdateSource, occupancy, getTableById, occupancyTableId } = props;
  const [ table, setTable] = useState("");
  const occupancyPercent = Number((((occupancy?.count || 0)/ maxOccupancy) * 100).toFixed(2));

   useEffect(()=>{
   if(occupancyTableId)
      getTableById(occupancyTableId)
      .then((res : any)=>{
        if(res?.success)
          setTable(res?.garageOccupancyTable?.title || "");
      }) 
   }, [])
  return (
    <Card shadow minWidth="40vw">
      <Header textAlign="left">
        <Grid customCols="auto auto 1fr auto" placeSelf="end">
          <Text bold>{name}</Text>
          <Badge
            style={{ fontSize: ".65em" }}
            disabled={!active}
          >
            {active ? "Active" : "Disabled"}
          </Badge>
          <div></div>
          <span className="noselect">
            <i className="fa fa-edit pointer hoverr" onClick={edit}></i> &nbsp;
            <i className="fa fa-trash pointer" style={{ color: "gray" }}></i> &nbsp;
          </span>
        </Grid>
      </Header>
      <Text color="gray" textAlign="left">
        &nbsp; <i className="far fa-compass" ></i> {address}
      </Text>
      {table && <Text color="gray" textAlign="left">
        &nbsp; <i className="fa fa-table" ></i> {table}
      </Text>}
      
      <Grid customCols="auto 1fr">
        <i className="fa fa-car txt-lgx" aria-hidden="true"></i>
        <Grid placeSelf="stretch" placeItems="center end" gridGap=".5em">
          <Progress
            strokeWidth={15}
            successPercent={0}
            strokeColor={primaryColor}
            percent={occupancyPercent}
            style={{
              maxWidth: "90%",
              placeSelf: "center end",
              width: "300px",
              paddingRight: '.5em'
            }}
          />
          <Badge 
            disabled={!occupancy?.count} 
            clasName={"grid "} 
            style={{ placeSelf: "end", marginRight: "2em" }}
          >
            <div>
              {occupancy?.count || "N/A"} <span style={{ fontSize: "1.3em" }}> / </span>{maxOccupancy}
            </div>
          </Badge>
          {occupancy?.updatedAt && 
            <div style={{color: "grey", marginRight: "2em", fontSize: ".75em"}}>
              Last Updated: {moment(occupancy.updatedAt).toString().substring(4,24)}
            </div>
          }
        </Grid>
        <Badge inverted>{occupancyUpdateSource === 'integration' ? "Automatic" : "Manual"}</Badge>
      </Grid>
    </Card>
  );
};

export default GarageCard;
