import React from "react";
import { Card } from "../../style";
import { Table } from "antd";
import { getColor } from "./utils/color";

export default ({ timeSeries, show } : any) => {
  const tableProps = {
    loading: timeSeries? false : true,
    style: {
      width: '100%'
    },
    dataSource: timeSeries?.map(({ displayLabel: label }: any, key: number) => {
      return { label, key, color: getColor(key) }
    }),

    columns: [
      {
        title: 'Garage',
        dataIndex: 'label',
        key: 'label',
        render: (garage: string, index: Number) =><span>{garage}</span>
      },
      {
        title: 'Color',
        dataIndex: 'color',
        key: 'color',
        render: (color: string, index: Number) => <span className='round' style={{ background: color }}></span>
      }
    ]
  }

  if(!show) return null;
  return (
        <Card shadow width="100%" >
          <Table
            pagination={false}
            {...tableProps}
          />
        </Card>
  )
};